.dialogs {
    display: grid;
    grid-template-columns: 2fr 10fr;
}
.dialogs a {
    color: white;
    text-decoration: none;
}
.messageItem {
    color: white;
}

.dialogs a.activeLink {
    color: gold;
}
.messageTextarea {
    padding-top: 1em;
}

.error {
    color: red;
    font-weight: 1000;
    font-size: 12px;
}